@import 'variable';

.card {
  padding: 32px 16px 16px;
  width: 200px;
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;

  &__photo {
    display: block;
    height: 134px;
    width: 160px;
    margin-inline: auto;
    margin-bottom: 40px;
  }

  &__description {
    display: block;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    color: $main-color;

    margin-bottom: 4px;
  }

  &__code {
    font-size: 10px;
    line-height: 14px;
    color: $secondary-color;

    margin-top: 0;
    margin-bottom: 16px;
  }

  &__ratings {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;

    &-review {
      display: flex;
      margin: 0;
      line-height: 16px;
      font-size: 10px;
      color: $main-color;
      position: relative;
      top: 1px;
    }
  }

  &__price {
    height: 18px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;

    &-name {
      font-size: 12px;
      color: $secondary-color;
    }

    &-value {
      font-size: 16px;
      line-height: 18px;
      font-weight: 700;
      color: $main-color;
    }
  }

  &__buy {
    display: flex;
    align-items: center;
    justify-content: center;

    height: 40px;
    line-height: 16px;
    text-align: center;

    font-size: 14;
    font-weight: 700;
    text-transform: uppercase;

    border-radius: 5px;
    border: 1px solid $blue-color;
    box-sizing: border-box;
    text-decoration: none;
    color: #fff;
    background-color: $blue-color;

    &:hover {
      color: $blue-color;
      background-color: #fff;
    }
  }
}
